// NotificationsPage.tsx - דף התראות
import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Bell, CheckCircle, AlertTriangle, Info, XCircle,
  RefreshCw, ExternalLink, Loader2
} from 'lucide-react';
import { apiGet, apiPost } from '../utils/api';
import { logger } from '../utils/logger';

interface Notification {
  id: string;
  type: 'success' | 'warning' | 'info' | 'error';
  title: string;
  message: string;
  created_at: string;
  read: boolean;
  action_url?: string;
}

export default function NotificationsPage() {
  const navigate = useNavigate();
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchNotifications = useCallback(async () => {
    try {
      setLoading(true);
      const data = await apiGet('/api/notifications', { showErrorToast: false });
      setNotifications(data || []);
      setError(null);
    } catch (err) {
      logger.error('Failed to fetch notifications:', err);
      setError('שגיאה בטעינת ההתראות');
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchNotifications();
  }, [fetchNotifications]);

  const markAsRead = async (notificationId: string) => {
    try {
      await apiPost(`/api/notifications/${notificationId}/read`, {}, { showErrorToast: false });
      setNotifications(prev =>
        prev.map(n => n.id === notificationId ? { ...n, read: true } : n)
      );
    } catch (err) {
      logger.error('Failed to mark notification as read:', err);
    }
  };

  const handleNotificationClick = (notification: Notification) => {
    markAsRead(notification.id);
    if (notification.action_url) {
      navigate(notification.action_url);
    }
  };

  const getIcon = (type: string) => {
    switch (type) {
      case 'success': return <CheckCircle className="text-green-400" size={24} />;
      case 'warning': return <AlertTriangle className="text-amber-400" size={24} />;
      case 'error': return <XCircle className="text-red-400" size={24} />;
      default: return <Info className="text-blue-400" size={24} />;
    }
  };

  const getTypeStyle = (type: string) => {
    switch (type) {
      case 'success': return 'border-green-500/30 bg-green-500/10';
      case 'warning': return 'border-amber-500/30 bg-amber-500/10';
      case 'error': return 'border-red-500/30 bg-red-500/10';
      default: return 'border-blue-500/30 bg-blue-500/10';
    }
  };

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr);
    const now = new Date();
    const diff = now.getTime() - date.getTime();
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const days = Math.floor(hours / 24);

    if (hours < 1) return 'לפני פחות משעה';
    if (hours === 1) return 'לפני שעה';
    if (hours < 24) return `לפני ${hours} שעות`;
    if (days === 1) return 'אתמול';
    return `לפני ${days} ימים`;
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen" dir="rtl">
        <div className="text-center">
          <Loader2 className="animate-spin h-12 w-12 text-blue-400 mx-auto mb-4" />
          <p className="text-blue-300">טוען התראות...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="p-8 max-w-3xl mx-auto" dir="rtl">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div className="flex items-center gap-3">
          <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-purple-600 rounded-xl flex items-center justify-center">
            <Bell className="text-white" size={28} />
          </div>
          <div>
            <h1 className="text-3xl font-bold text-white">התראות</h1>
            <p className="text-blue-300">
              {notifications.filter(n => !n.read).length} התראות שלא נקראו
            </p>
          </div>
        </div>
        <button
          onClick={fetchNotifications}
          className="flex items-center gap-2 px-4 py-2 bg-white/10 text-white rounded-lg hover:bg-white/20 transition-colors"
        >
          <RefreshCw size={18} />
          <span>רענן</span>
        </button>
      </div>

      {error && (
        <div className="bg-red-500/10 border border-red-500/30 rounded-lg p-4 mb-6 text-red-300">
          {error}
        </div>
      )}

      {/* Notifications List */}
      {notifications.length === 0 ? (
        <div className="text-center py-16">
          <Bell className="mx-auto mb-4 text-gray-500" size={64} />
          <h2 className="text-xl font-semibold text-white mb-2">אין התראות</h2>
          <p className="text-gray-400">כשיהיו עדכונים חשובים, הם יופיעו כאן</p>
        </div>
      ) : (
        <div className="space-y-4">
          {notifications.map((notification) => (
            <div
              key={notification.id}
              onClick={() => handleNotificationClick(notification)}
              className={`relative rounded-xl p-5 border cursor-pointer transition-all hover:scale-[1.01] ${
                getTypeStyle(notification.type)
              } ${notification.read ? 'opacity-60' : ''}`}
            >
              <div className="flex items-start gap-4">
                {getIcon(notification.type)}
                <div className="flex-1">
                  <div className="flex items-center justify-between mb-1">
                    <h3 className="text-white font-semibold">{notification.title}</h3>
                    <span className="text-xs text-gray-400">{formatDate(notification.created_at)}</span>
                  </div>
                  <p className="text-gray-300 text-sm">{notification.message}</p>
                  {notification.action_url && (
                    <div className="flex items-center gap-1 mt-2 text-blue-400 text-sm">
                      <ExternalLink size={14} />
                      <span>לחץ לפרטים</span>
                    </div>
                  )}
                </div>
                {!notification.read && (
                  <div className="absolute top-4 left-4">
                    <div className="w-3 h-3 bg-blue-500 rounded-full animate-pulse" />
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
